body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}
.tic-tac-toe {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  position: relative; }
  .tic-tac-toe__game-field {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    grid-template-areas: ". . ." ". . ." ". . .";
    box-shadow: 0px 0px 7px 0px rgba(0, 0, 0, 0.75); }
  .tic-tac-toe__cell {
    width: 150px;
    height: 150px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    font-size: 72px;
    background-color: #eaeae1;
    border: 1px solid #c0c0a5;
    color: #3d3d29; }
  .tic-tac-toe__refresh-button {
    margin: 30px;
    padding: 10px 20px;
    background-color: #009933;
    border-radius: 5px;
    font-size: 22px;
    color: white; }
  .tic-tac-toe__win-message {
    display: block;
    position: absolute;
    margin-left: auto;
    margin-right: auto;
    top: 20%;
    left: 0;
    right: 0;
    font-size: 75px;
    color: #009933; }

